@import "https://fonts.googleapis.com/css2?family=Jersey+25&display=swap";
@import "https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  font-family: Poppins;
  overflow-x: hidden;
}

.gallery-background-video {
  background: linear-gradient(#000000d2, #0000);
  width: 100%;
  height: 600px;
  position: absolute;
  top: 0;
  overflow: hidden;
}

.gallery-background-video video {
  z-index: -1;
  width: 100%;
  height: auto;
  min-height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.section1 {
  margin: 80px 10px 10px;
  padding: 10px;
  position: relative;
}

.intro-title {
  text-align: center;
  color: #fff;
  letter-spacing: 1px;
  font-family: "Jersey 25", sans-serif;
  font-size: 3rem;
  animation: 1s forwards fly-safety-main-intro;
}

.gallery-images {
  backdrop-filter: blur(100px);
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-content: center;
  place-items: center;
  gap: 20px;
  margin-top: 70px;
  padding: 20px;
  display: grid;
}

.gallery-images img {
  object-fit: cover;
  cursor: pointer;
  width: 95%;
  height: 300px;
  transition: all .5s;
}

.gallery-images img:hover {
  transform: scale(1.05);
}
